html,
body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.grid {
  margin-top: 100px;
  padding-top: 5px;
  border-left: 10px solid #017a7f;
  border-right: 10px solid #c70000;
  background-image: linear-gradient(
    90deg,
    rgba(1, 123, 127, 0.123),
    rgba(199, 0, 0, 0.123)
  );
}
#root {
  height: 100%;
}
