@font-face {
  font-family: abold;
  src: url("./Avenir/AvenirLTStd-Black.otf");
}
@font-face {
  font-family: areg;
  src: url("./Avenir/AvenirLTStd-Book.otf");
}
.navbar {
  width: 100%;
  position: fixed;
  top: 0;
  height: 150px;
  display: flex;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.5);
  user-select: none;
}
.logo {
  background-image: url("./Images/logo.png");
  background-clip: border-box;
  height: 65px;
  width: 220px;
  background-repeat: no-repeat;
  background-size: cover;
  margin-left: 7%;
}
.buttons {
  height: fit-content;
  width: fit-content;
  margin-left: auto;
  display: flex;
  margin-right: 7%;
}
.visualise-btn {
  padding: 10px 8px;
  background-color: rgba(104, 166, 166, 0.2);
  border-radius: 0px;
  margin-left: auto;
  margin-left: 30px;
  transition: all 0.2s;
  border: 1px solid transparent;
}
.visualise-btn:not(.btn-disabled):hover {
  background-color: rgba(104, 166, 166, 0.4);
  cursor: pointer;
  border-color: rgba(1, 123, 127, 0.815);
}
.visualise-btn:not(.btn-disabled):active {
  transform: scale(1.05);
}
.btn-disabled {
  background-color: rgba(163, 163, 163, 0.144);
  border-color: rgba(116, 116, 116, 0.452);
  opacity: 0.5;
}
.bold {
  font-weight: 700;
}
