.node {
  width: 25px;
  height: 25px;
  outline: 1px solid rgba(12, 53, 71, 0.1); /* rgb(175, 216, 248) */
  display: inline-block;
  box-sizing: content-box;
  margin-top: -2px;
  margin-right: 1px;
  overflow: hidden;
}
.node-icon {
  font-size: 20px;
  color: rgb(12, 53, 71);
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.node-end {
  animation: nodeAppear 0.7s ease-out forwards;
}

.node-start {
  animation: nodeAppear 0.7s ease-out forwards;
}

.node-visited {
  animation: visitedAnim 1.5s ease-out alternate forwards running;
}
.node-marker {
  animation: currentAnim 0.05s;
}

.node-wall {
  background-color: grey;
  animation: wallAnim 0.6s ease-out alternate forwards running;
}

.node-path {
  animation: pathAnim 1.5s ease-out alternate forwards running;
}

.node-path-static {
  background-color: rgb(255, 254, 106);
}

@keyframes nodeAppear {
  0% {
    transform: scale(1);
    outline-color: transparent;
  }
  50% {
    transform: scale(1.4);
    outline-color: transparent;
    background-color: rgba(0, 0, 0, 0.05);
  }
  100% {
    transform: scale(1);
    outline-color: transparent;
    background-color: rgba(0, 0, 0, 0.05);
  }
}

@keyframes pathAnim {
  0% {
    outline-color: transparent;
    transform: scale(0.7);
    background-color: white;
  }
  50% {
    outline-color: transparent;
    transform: scale(1.2);
    background-color: white;
  }
  100% {
    outline-color: white;
    transform: scale(1);
    background-color: white;
  }
}

@keyframes currentAnim {
  0% {
    background-color: white;
    outline-color: transparent;
    transform: scale(1);
  }
  100% {
    background-color: inherit;
    border-radius: inherit;
    outline-color: transparent;
  }
}

@keyframes visitedAnim {
  0% {
    outline-color: transparent;
    transform: scale(0.3);
    background-color: rgba(1, 123, 127, 0.383);
    border-radius: 100%;
  }
  50% {
    outline-color: transparent;
    background-color: rgba(145, 174, 175, 0.383);
  }
  75% {
    outline-color: transparent;
    transform: scale(1.2);
    background-color: #e0a6a665;
  }
  100% {
    transform: scale(1);
    outline: 1px solid #c7000025;
    background-color: #c700004b;
  }
}

