@keyframes wallAnim {
  0% {
    transform: scale(1);
    background-color: rgba(12, 53, 71, 0.1);
  }
  25% {
    transform: scale(1.2);
    background-color: rgba(12, 53, 71, 0.1);
  }
  50% {
    transform: scale(1);
    background-color: rgba(12, 53, 71, 0.1);
  }
  100% {
    background-color: rgba(12, 53, 71, 0.1);
  }
}
